eniorenheimen unserer Region. Seit über 10 Jahren bin ich im Bereich Rehabilitation für psycho/somatische Erkrankungen, Kardiologie und post/longCovid tätig. Meine Ausbildung zur Heilpraktikerin konnte ich 2017 mit der amtsärztlichen Überprüfung in Potsdam erfolgreich beenden. Weiterbildungen spielten und spielen in meinem Leben eine zentrale Rolle. Vorerst absolvierte ich Fortbildungen in Manueller Lymphdrainage, Nordic Walking, komplementären physiotherapeutischen Therapien an der medizinischen Hochschule in Hannover. Später entdeckte ich die Leidenschaft an der traditionellen chinesischen Medizin. Es folgten Ausbildungen in der TUINA, Akupunktur, Narbenentstörung, Schröpfen an der Deutschen TUINA Akademie in Leipzig, und dem Qigong am ShenMen-Institut. Ernährung und Kräutertherapien sind ein weiterer Schwerpunkt. Seit 11/2022 betreibe ich nebenberuflich meine eigene Praxis für komplementäre physiotherapeutische Therapie und traditionelle chinesische Medizin in Stahnsdorf/ Kienwerder, Alte Feldmark 11a. ")]),_c('a',{staticClass:"about-praxis",attrs:{"href":"mailto: sabine.hahm@kp-tcm-hahm.de","target":"_blank"}},[_vm._v("Kontakt")])])])]),_c('div',{staticClass:"projects-sec",attrs:{"id":"galarie"}},[_c('h2',[_vm._v("Galerie")]),_c('p',{staticClass:"line"}),_c('div',{staticClass:"row"},[_c('img',{staticClass:"col-lg-4 resize-img resize-img-top",attrs:{"src":"project-13.png","alt":""}}),_c('img',{staticClass:"col-lg-4 resize-img",attrs:{"src":"project-2.png","alt":""}}),_c('img',{staticClass:"col-lg-4 resize-img",attrs:{"src":"project-14.png","alt":""}})]),_c('div',{staticClass:"row"},[_c('img',{staticClass:"col-lg-6 resize-img-center",attrs:{"src":"project-15.png","alt":""}}),_c('img',{staticClass:"col-lg-6 resize-img-center",attrs:{"src":"project-5.png","alt":""}})]),_c('div',{staticClass:"row"},[_c('img',{staticClass:"col-lg-4 resize-img-bottom",attrs:{"src":"project-9.png","alt":""}}),_c('img',{staticClass:"col-lg-4 resize-img",attrs:{"src":"project-10.png","alt